.vm-article-title {
  font-size: 1.675rem;
  line-height: 44px;
}
.vm-article-container {
  background: white;
  margin-top: 0.315rem;
  padding: 1.25rem 0.94rem;
}
.vm-author-info-container {
  margin: 0.875rem 0;
}
.vm-author-head {
  width: 1.56rem;
  margin-right: 0.575rem;
  display: flex;
}
.vm-author-head img {
  width: 1.56rem;
  height: 1.56rem;
  border-radius: 100%;
  margin: auto;
}
.vm-author-name {
  font-weight: 800;
  font-size: 0.75rem;
  margin-bottom: 5px;
}
.vm-author-date {
  color: #bdbdbd;
}
.vm-author-btn-container {
  display: flex;
}
.vm-author-btn {
  background: #14AAED;
  color: white;
  padding: 0 0.625rem;
  vertical-align: middle;
  border-radius: 5px;
  height: 1.44rem;
  line-height: 1.44rem;
  margin: auto;
}
.vm-article-content {
  font-size: 0.815rem;
  line-height: 1.5rem;
}
.vm-comment-row-container {
  margin-top: 0.315rem;
  padding: 0.94rem 1.25rem;
  background: white;
}
.vm-comment-row-container * {
  vertical-align: middle;
}
.vm-comment-row-container img {
  width: 0.875rem;
  height: 0.875rem;
  margin-right: 0.47rem;
}
.vm-comment-row-container span {
  color: #bdbdbd;
  margin-right: 0.47rem;
  font-size: 0.815rem;
}
