.sticky-list .sticky-container .am-list-item {
  padding-left: 0;
}
.sticky-list .sticky-container .am-list-line {
  padding-right: 0;
}
.sticky-list .sticky-container .am-list-line .am-list-content {
  padding-top: 0;
  padding-bottom: 0;
}
.sticky-list .sticky-container .sticky {
  padding: 7px 15px;
  transform: none;
}
.am-indexed-list-container {
  height: 100%;
}
.vm-tab-bar-container {
  position: absolute;
  bottom: 0;
  width: 100%;
  border-top: 1px solid #cccccc;
  background: white;
  cursor: pointer;
}
.vm-tab-bar-item {
  flex: 1;
  padding: 0.3rem 0;
  text-align: center;
  height: 2.63rem;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.vm-search-bar {
  width: 100%;
  height: 1.62rem;
  background: #f0f0f0;
  border-radius: 3.12rem;
  border: none;
  outline: none;
  padding-left: 1.84rem;
  font-size: 0.88rem;
}
.vm-search-bar-Container {
  background: white;
  padding: 0.47rem 0.94rem;
  border-bottom: 1px solid rgba(240, 240, 240, 0.5);
}
.vm-search-bar-icon {
  position: absolute;
  left: 0.625rem;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 0.745rem;
  height: 0.745rem;
}
.vm-search-bar-div {
  position: relative;
}
.vm-tab-bar-icon-container {
  height: 1.25rem;
  margin-bottom: 0.15rem;
}
.vm-tab-bar-icon-container .vm-tab-bar-icon {
  width: 0.94rem;
  height: 0.94rem;
  vertical-align: middle;
}
.vm-tab-bar-title {
  height: 0.78rem;
  font-size: 0.57rem;
  color: #363636;
}
.vm-search-bar-container-div {
  background: white;
}
.vm-search-bar-shadow {
  margin: 0 0.94rem 0.94rem 0.94rem;
}
.vm-search-bar-shadow .vm-search-bar {
  height: 2.19rem;
  background: white;
  -webkit-appearance: none;
  box-shadow: 0 0 15px 5px rgba(226, 226, 226, 0.7);
  border-radius: 0.32rem;
}
